.home{
  padding: 0 10%;
}

.cardHome{
  display: flex;
  background-color: #fff;
  justify-content: space-around;
  align-items: center;
}

.cardHome:nth-child(even){
  background-color: #eee;
}

.cardHome:hover{
  background-color: lightgray;
}

.cardHome .left{
  flex: 0.8;
  color: black;
  text-decoration: none;
}

.cardHome .left .title{
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
}

.cardHome .left .body{
  font-size: 14px;
  font-size: 400;
}

.cardHome .right .icon {
  height: 24px;
  width: 24px;
  color: red;
  opacity: 0.8;
}